import "./styles.css";
import * as React from "react";

import RX30 from "./RX2";

import { Accordion } from "@mui/material";
import { AccordionSummary } from "@mui/material";
import { AccordionDetails } from "@mui/material";
import { Typography } from "@mui/material";

export default function KENSAKU2() {
  return (
    <div>
      <Accordion className="AAAX">
        <AccordionSummary>
          <Typography>特殊系統 検索</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <input
            type="radio"
            name="categories"
            id="All"
            value="All"
            checked
          ></input>
          <label for="All"> ぜんぶ </label>
          <br></br>
          <input
            type="radio"
            name="categories"
            id="Gundamkei"
            value="Gundamkei"
          ></input>
          <label for="Gundamkei"> ガンダム系 </label>
          <br></br>
          <input type="radio" name="categories" id="AGE" value="AGE"></input>
          <label for="AGE"> AGEガンダム系 </label>
          <br></br>
          <input
            type="radio"
            name="categories"
            id="Renpoukei"
            value="Renpoukei"
          ></input>
          <label for="Renpoukei"> 連邦系 </label>
          <br></br>
          <input
            type="radio"
            name="categories"
            id="Jionkei"
            value="Jionkei"
          ></input>
          <label for="Jionkei"> ジオン系 </label>
          <br></br>
          <input
            type="radio"
            name="categories"
            id="Veigankei"
            value="Veigankei"
          ></input>
          <label for="Veigankei"> ヴェイガン系 </label>
          <br></br>
          <input
            type="radio"
            name="categories"
            id="AkaiMS"
            value="AkaiMS"
          ></input>
          <label for="AkaiMS"> 赤いMS </label>
          <br></br>
          <input
            type="radio"
            name="categories"
            id="KuroiMS"
            value="KuroiMS"
          ></input>
          <label for="KuroiMS"> 黒いMS </label>
          <br></br>

          <br></br>
          <br></br>
          <br></br>

          <RX30 />
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
